import React, {PureComponent} from 'react';
import SignaturePad from 'react-signature-pad-wrapper';
import { Form, Row, Col, Button } from 'react-bootstrap';
import DirectionProvider, { DIRECTIONS } from 'react-with-direction/dist/DirectionProvider';
import htmlToImage from 'html-to-image';

class App extends PureComponent {

    handleClear() {
        this.signaturePad.instance.clear();
    }

    handleSave() {
        var node = document.getElementById('root');
        htmlToImage.toPng(node)
          .then(function (dataUrl) {
            var a = document.createElement("a");
            a.href = dataUrl;
            a.download = "Image.png";
            a.click();
          })
          .catch(function (error) {
            console.error('oops, something went wrong!', error);
          });
    }

    renderTitle() {
        return (
            <div className="columns">
                <div className="column">
                    <h1 className="title">הצהרת בריאות לתלמיד</h1>
                    <br/>
                </div>
            </div>
        );
    }

    renderForm() {
        return (
            <div>
                <Form>
                    <Form.Group as={Row} controlId="frmChildName">
                        <Form.Label column sm={2}>שם התלמיד/ה</Form.Label>
                        <Col sm={4}>
                            <Form.Control type="text" placeholder="ג׳ון דו" />
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="frmChildID">
                        <Form.Label column sm={2}>מס׳ תעודת זהות</Form.Label>
                        <Col sm={4}>
                            <Form.Control type="text" placeholder="301234567" />
                        </Col>
                    </Form.Group>

                    <Form.Group controlId="frmChkFever">
                        <Form.Check type="checkbox" label="מדדתי חום לילד/תי ונמצא כי חום גופו/ה מתחת ל-38 מעלות צלזיוס" />
                    </Form.Group>
                    <Form.Group controlId="frmChkCough">
                        <Form.Check type="checkbox" label="ילד/תי לא משתעל/ת ואין לו/לה קשיים בנשימה*" />
                    </Form.Group>
                    <Form.Group controlId="frmCorona">
                        <Form.Check type="checkbox" label="למיטב ידיעתי ילד/תי לא היה/הייתה במגע קרוב עם חולה קורונה בשבועיים האחרונים" />
                    </Form.Group>

                    <Form.Group as={Row} controlId="frmParentName">
                        <Form.Label column sm={2}>שם ההורה</Form.Label>
                        <Col sm={4}>
                            <Form.Control type="text" placeholder="ג׳ון דו האב" />
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="frmParentId">
                        <Form.Label column sm={2}>מס׳ תעודת זהות</Form.Label>
                        <Col sm={4}>
                            <Form.Control type="text" placeholder="301234567" />
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="frmDate">
                        <Form.Label column sm={2}>תאריך</Form.Label>
                        <Col sm={4}>
                            <Form.Control type="text" placeholder="01/01/2020" />
                        </Col>
                    </Form.Group>
                </Form>
            </div>
        )
    }

    renderSignaturePad() {
        return (
            <div className="columns">
                <div className="column">
                    <Form.Group as={Row} controlId="frmDate">
                        <Form.Label column sm={2}>חתימת ההורה</Form.Label>
                        <Col sm={4}>
                            <div className="card">
                                <div className="card-content">
                                    <div className="content">
                                        <SignaturePad
                                            redrawOnResize={true}
                                            ref={ref => this.signaturePad = ref} />
                                    </div>
                                </div>
                                <footer className="card-footer">
                                    <a
                                        className="card-footer-item"
                                        onClick={this.handleClear.bind(this)}
                                    >
                                        נקה חתימה
                                    </a>
                                </footer>
                            </div>
                        </Col>
                    </Form.Group>
                </div>
            </div>
        );
    }

    renderClarifications() {
        return (
            <div className="columns">
                <div className="column">
                    <span>* למעט שיעול או קושי בנשימה הנובע ממצב כרוני כגון אסטמה או אלרגיה אחרת</span>
                </div>
            </div>
        );
    }

    renderSave() {
        return (
            <div className="columns">
                <div className="column">
                    <Button style={{ marginTop: 20 }}variant="outline-dark" onClick={this.handleSave.bind(this)}>שמור כתמונה לשיתוף</Button>
                </div>
            </div>
        )
    }

    render() {
        return (
            <DirectionProvider direction={DIRECTIONS.RTL}>
                <section className="section">
                    <div className="container">
                        {this.renderTitle()}
                        {this.renderForm()}
                        {this.renderSignaturePad()}
                        {this.renderClarifications()}
                        {this.renderSave()}
                    </div>
                </section>
            </DirectionProvider>
        );
    }
}

export default App;
